import React, { useState } from "react";

export const Categories = (props) => {
  const filterdData = new Set(props.data.map((item) => item.category)); //get unique data using es6 Set
  const filteredArray = ["all", ...filterdData]; //put fitlered data in array

  const tabFuntion = (item) => {
    props.filterData(item);
  };
  return (
    <div className="row">
      <div className="col-md-12">
        <ul className="category-list">
          {filteredArray.map((item) => {
            return (
              <li onClick={() => tabFuntion(item)}>
                {item}  
              </li>
            );
          })}
        </ul>
      </div>
    </div>
  );
};
