import { Heading } from "./Heading";
import { Categories } from "./Categories";
import { Menu } from "./Menu";
import { Data } from "./Data";
import "./App.css";
import { useState } from "react";

function App() {
  const [menu, setMenu] = useState(Data);
  const filterData = (text) => {
    const newArr = Data.filter((item) => item.category === text);
    setMenu(newArr);  
    if(text === 'all'){
      setMenu(Data);
      return;
    }
  };

  return (
    <div className="App">
      <div className="container">
        <div className="row">
          <div className="col-md-12">
            <Heading />
            <Categories filterData={filterData} data={Data}/>
            <Menu menuProps={menu} />
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
